<script>
	import { getContext } from 'svelte';

	const { data, xGet, yGet, zGet, xScale, yScale, xRange, yRange, xDomain, yDomain } = getContext('LayerCake');

	$: path = values => {
		return 'M' + values
			.map(d => {
				return $xGet(d) + ',' + $yGet(d);
			})
			.join('L');
	};
	
	export let hovered = null;
	export let selected = null;
	let hoveredLine;
	let selectedLine;
	
	function hover(key) {
		hovered = key;
	}
	
	function select(key) {
		selected = key;
	}
	
	$: if (selected) {
		let obj = $data.find(o => o.key === selected);
		if (obj) {
			selectedLine = path(obj.values);
		} else {
			selectedLine = null;
		}
	}
	$: if (hovered) {
		let obj = $data.find(o => o.key === hovered);
		if (obj) {
			hoveredLine = path(obj.values);
		} else {
			hoveredLine = null;
		}
	}
</script>

<g class="line-group">
	{#each $data as group}
		<path
			class='path-line'
			d='{path(group.values)}'
			stroke="{$zGet(group)}"
			on:click={() => select(group.key)}
			on:mouseover={() => hover(group.key)}
			on:mouseleave={() => hover(null)}
		></path>
	{/each}
	{#if hovered}
	<path
			class='path-line active muted'
			d='{hoveredLine}'
		></path>
	{/if}
	{#if selected}
	<path
			class='path-line active'
			d='{selectedLine}'
		></path>
	{/if}
</g>

<style>
	.path-line {
		fill: none;
		stroke-linejoin: round;
		stroke-linecap: round;
		stroke-width: 1px;
	}
	.active {
		stroke-width: 2px;
		stroke: #206095 !important;
		z-index: 100;
		pointer-events: none;
	}
	.muted {
		stroke-opacity: 0.5;
	}
</style>