<script>
	import Panel from './Panel.svelte';
	import Map from './Map.svelte';
	import MapSource from './MapSource.svelte';
	import MapLayer from './MapLayer.svelte';
	import Chart from './Chart.svelte';
	import Select from './Select.svelte';
	import { getTopo, getTotals, getSeries, getCentroids, getBreaks, pivotData } from './utils.js';
	
	const mapstyle = 'https://bothness.github.io/ons-basemaps/data/style-omt.json';
	const topojson = {
		url: 'https://bothness.github.io/geo-data/topojson/lac2019ss.topojson',
		layer: 'lac2019',
		id: 'AREACD'
	};
	const colors = [
		'rgb(127, 205, 187)',
		'rgb(65, 182, 196)',
		'rgb(29, 145, 192)',
		'rgb(34, 94, 168)',
		'rgb(12, 44, 132)'
	];
	
	let map = null;
	
	let data = {
		boundaries: null,
		centroids: null,
		totals: null,
		breaks: null,
		series: null,
		lookup: null
	};
	
	let highlight = {
		hovered: null,
		selected: null,
		multi: null
	};

	let loaded = false;
	
	// Load topojson data to geojson object
	getTopo(topojson.url, topojson.layer)
	.then(result => {
		data.boundaries = result;
		return data.boundaries;
	})
	.then(boundaries => {
		// Create LAD code/name lookup from geojson
		let lookup = {};
		for (let i in boundaries.features) {
			lookup[boundaries.features[i].properties['AREACD']] = boundaries.features[i].properties['AREANM'];
		}
		data.lookup = lookup;

		return boundaries;
	})
	.then(boundaries => {
		// Generate centroids from geojson
		data.centroids = getCentroids(boundaries);

		return data.centroids;
	})
	.then(() => {

		// Load totals from API
		getTotals().then(result => {
			data.totals = result;
			return data.totals;
		})
		.then(totals => {
			data.breaks = getBreaks(totals);
			return data.breaks;
		})
		.then(() => {

			// Load time series from API
			getSeries().then(result => {
				data.series = pivotData(result);

				return data.series;
			})
			.then(() => {
				loaded = true;
			});
		});
	});

	// Select function, if required
	function doSelect() {
		return true;
	}
	
</script>

<style>
	:global(body) {
		margin: 0;
		padding: 0;
	}
	main {
		width: 100%;
		padding: 12px;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	small {
		color: #999;
	}
	.chart {
		width: 100%;
		height: 200px;
		margin-top: 20px;
		margin-bottom: 5px;
	}
</style>

{#if loaded}
<Panel>
	<main>
		<h1>Covid-19 in Care Homes</h1>
		{#if data.lookup}
		<Select options={data.lookup} bind:selected={highlight.selected} search={true} on:select={doSelect} />
		{/if}
		{#if highlight.selected && data.totals && data.totals.find(d => d.geocode == highlight.selected)}
		<p>{data.totals.find(d => d.geocode == highlight.selected).value} care home deaths</p>
		{:else}
		<p>No data on care home deaths</p>
		{/if}
		{#if data.series}
		<div class="chart">
			<Chart data={data.series} bind:hovered={highlight.hovered} bind:selected={highlight.selected} />
		</div>
		{/if}
		<p><small>Data aggregated by COGS from national data for Northern Ireland, Scotland, Wales and England.</small></p>
	</main>
</Panel>

<Map style={mapstyle} minzoom={3} maxzoom={13}>
	{#if data.boundaries}
	<MapSource id="lad" type="geojson" data={data.boundaries} promoteId={topojson.id}>
		<MapLayer id="fill" source="lad" sibling="lad-point" type="fill" bind:hovered={highlight.hovered} bind:selected={highlight.selected} paint="{{'fill-color': 'rgba(255, 255, 255, 0)'}}" click={true} hover={true} />
		<MapLayer id="line" source="lad" type="line" highlight={true} highlighted={highlight.multi} paint="{{
			'line-color': ['case',
				['==', ['feature-state','selected'], true], 'rgb(0, 0, 0)',
				['==', ['feature-state','hovered'], true], 'rgb(72, 72, 72)',
				'rgb(128, 128, 128)'
			],
			'line-width': ['case',
				['==', ['feature-state','selected'], true], 2,
				['==', ['feature-state','hovered'], true], 1,
				0.5
			]
		}}" />
	</MapSource>
	{/if}
	{#if data.centroids && data.totals && data.breaks}
	<MapSource id="lad-point" type="geojson" data={data.centroids} promoteId={topojson.id}>
		<MapLayer id="lad-point" source="lad-point" type="circle" data={data.totals} breaks={data.breaks} {colors} paint="{{
		'circle-color': ['case',
			['!=', ['feature-state','color'], null],
		  ['feature-state','color'],
			'rgba(255, 255, 255, 0)'
		],
		'circle-radius': ['case',
		  ['!=', ['feature-state', 'value'], null],
		  ['*', ['sqrt', ['/', ['feature-state', 'value'], data.breaks[5]]], 14],
		  0
		],
		'circle-stroke-color': ['case',
			['==', ['feature-state','selected'], true], 'rgb(0, 0, 0)',
			['==', ['feature-state','hovered'], true], 'rgb(72, 72, 72)',
			'rgba(255, 255, 255, 0)'
		],
		'circle-stroke-width': ['case',
			['==', ['feature-state','selected'], true], 2,
			['==', ['feature-state','hovered'], true], 1,
			0.5
		],
		'circle-opacity': 0.8
		}}" />
	</MapSource>
	{/if}
</Map>
{/if}