<script>
	import mapbox from 'mapbox-gl';
	import { bbox } from '@turf/turf';
	import { getContext } from 'svelte';
	import { getColor } from './utils.js';
	
	export let id;
	export let source;
	export let sibling = null;
	export let sourceLayer = null;
	export let type;
	export let layout = {};
	export let paint = {};
	export let data = null;
	export let breaks = null;
	export let colors = null;
	export let selected = null;
	export let hovered = null;
	export let highlighted = null;
	export let click = false;
	export let hover = false;
	export let highlight = false;
	export let order = 'place_other';
	
	export let bounds = [[-10.653041, 49.815042], [1.643370, 60.928626]]; // GB & Ireland bounding box
	
	const { getMap } = getContext('map');
	const map = getMap();
	
	let selectedPrev = null;
	let highlightedPrev = null;
	
	if (map.getLayer(id)) {
    map.removeLayer(id);
	}
	
	let options = {
		'id': id,
		'type': type,
		'source': source,
		'paint': paint,
		'layout': layout
	};
	
	if (sourceLayer) {
		options['source-layer'] = sourceLayer;
	}
	
	map.addLayer(options, order);
	
	$: if (data && breaks && colors && map.getLayer(id)) {
		for (let i in data) {
			if (data[i].geocode) {
				map.setFeatureState({
					source: source,
					sourceLayer: sourceLayer,
					id: data[i].geocode
				}, {
					color: getColor(colors, breaks, data[i].value),
					value: data[i].value
				});
			}
		}
	}
	
	if (click) {
		map.on('click', id, (e) => {
			e.preventDefault();
      if (e.features.length > 0) {
        if (selected) {
          map.setFeatureState(
            { source: source, sourceLayer: sourceLayer, id: selected },
            { selected: false }
          );
					if (sibling) {
						map.setFeatureState(
							{ source: sibling, sourceLayer: sourceLayer, id: selected },
							{ selected: false }
						);
					}
        }
        selected = e.features[0].id;
				
        map.setFeatureState(
          { source: source, sourceLayer: sourceLayer, id: selected },
          { selected: true }
        );
				if (sibling) {
					map.setFeatureState(
						{ source: sibling, sourceLayer: sourceLayer, id: selected },
						{ selected: true }
					);
				}
				
				// Fit to map feature
				let box = bbox(e.features[0].geometry);
				map.fitBounds(box, {padding: 20});
				
				selectedPrev = selected;
      }
    });
		
		map.on('click', (e) => {
			if (e.defaultPrevented === false) {
				if (selected) {
          map.setFeatureState(
            { source: source, sourceLayer: sourceLayer, id: selected },
            { selected: false }
          );
					if (sibling) {
						map.setFeatureState(
            	{ source: sibling, sourceLayer: sourceLayer, id: selected },
            	{ selected: false }
          	);
					}
        }
				map.fitBounds(bounds);
				
				selectedPrev = null;
				selected = null;
			}
		});
	}
	
	if (hover) {
		map.on('mousemove', id, (e) => {
      if (e.features.length > 0) {
        if (hovered) {
          map.setFeatureState(
            { source: source, sourceLayer: sourceLayer, id: hovered },
            { hovered: false }
          );
					if (sibling) {
						map.setFeatureState(
							{ source: sibling, sourceLayer: sourceLayer, id: hovered },
							{ hovered: false }
						);
					}
        }
        hovered = e.features[0].id;
        map.setFeatureState(
          { source: source, sourceLayer: sourceLayer, id: hovered },
          { hovered: true }
        );
				if (sibling) {
					map.setFeatureState(
						{ source: sibling, sourceLayer: sourceLayer, id: hovered },
						{ hovered: true }
					);
				}
				
        // Change the cursor style as a UI indicator.
        map.getCanvas().style.cursor = 'pointer';
      }
    });
		
		map.on('mouseleave', id, (e) => {
      if (hovered) {
        map.setFeatureState(
          { source: source, sourceLayer: sourceLayer, id: hovered },
          { hovered: false }
        );
				if (sibling) {
					map.setFeatureState(
						{ source: sibling, sourceLayer: sourceLayer, id: hovered },
						{ hovered: false }
					);
				}
      }
      hovered = null;

      map.getCanvas().style.cursor = '';
    });
	}

	$: if (selectedPrev != selected) {
		if (selectedPrev) {
			map.setFeatureState(
				{ source: source, sourceLayer: sourceLayer, id: selectedPrev },
				{ selected: false }
			);
			if (sibling) {
				map.setFeatureState(
					{ source: sibling, sourceLayer: sourceLayer, id: selectedPrev },
					{ selected: false }
				);
			}
		}
		
		if (selected) {
			map.setFeatureState(
				{ source: source, sourceLayer: sourceLayer, id: selected },
				{ selected: true }
			);
			if (sibling) {
				map.setFeatureState(
					{ source: sibling, sourceLayer: sourceLayer, id: selected },
					{ selected: true }
				);
			}
			let geojson = map.getSource(source)._data;
			let feature = geojson.features.filter(feature => feature.properties['AREACD'] == selected)[0];
			let box = bbox(feature);
			map.fitBounds(box, {padding: 20});
		} else {
			map.fitBounds(bounds);
		}
		
		selectedPrev = selected;
	}
	
	$: if (highlight) {
		if (highlighted) {
			if (highlightedPrev) {
				for (let i in highlightedPrev) {
					map.setFeatureState(
						{ source: source, sourceLayer: sourceLayer, id: highlightedPrev[i] },
						{ highlighted: false }
					);
				}
			}
			highlightedPrev = highlighted;
			for (let i in highlighted) {
				map.setFeatureState(
					{ source: source, sourceLayer: sourceLayer, id: highlighted[i] },
					{ highlighted: true }
				);
			}
		}
	}
	
</script>