<script>
	import { LayerCake, Svg, Html } from 'layercake';
	import { scaleOrdinal } from 'd3-scale';
	import { timeParse, timeFormat } from 'd3-time-format';
	import { format, precisionFixed } from 'd3-format';

	import MultiLine from './MultiLine.svelte';
	import AxisX from './AxisX.svelte';
	import AxisY from './AxisY.svelte';

	export let data;
	export let hovered;
	export let selected;

	/* --------------------------------------------
	 * Set what is our x key to separate it from the other series
	 */
	const xKey = 'day';
	const yKey = 'value';
	const zKey = 'key';

	const seriesNames = Object.keys(data[0]).filter(d => d !== xKey);
	const seriesColors = ['#dddddd'];

	const parseDate = timeParse('%Y-%m-%d');

	const dataLong = seriesNames.map(key => {
		return {
			key,
			values: data.map(d => {
				d[xKey] = typeof d[xKey] === 'string' ? parseDate(d[xKey]) : d[xKey]; // Conditional required for sapper
				return {
					key,
					[yKey]: +d[key],
					[xKey]: d[xKey]
				};
			})
		};
	});
	console.log(dataLong);

	// Make a flat array of the `values` of our nested series
	// we can pluck the `value` field from each item in the array to measure extents
	const flatten = data => data.reduce((memo, group) => {
		return memo.concat(group.values);
	}, []);

	const formatTickX = timeFormat('%d/%m');
	const formatTickY = d => format(`.${precisionFixed(d)}s`)(d);
</script>

<style>
	.chart-container {
		width: 100%;
		height: 100%;
	}
</style>

<div class="chart-container">
	<LayerCake
		padding={{ top: 7, right: 10, bottom: 20, left: 25 }}
		x={xKey}
		y={yKey}
		z={zKey}
		yDomain={[0, null]}
		zScale={scaleOrdinal()}
		zDomain={seriesNames}
		zRange={seriesColors}
		flatData={flatten(dataLong)}
		data={dataLong}
	>
		<Svg>
			<AxisX
				gridlines={false}
				ticks={5}
				formatTick={formatTickX}
				snapTicks={true}
			/>
			<AxisY
				ticks={5}
				formatTick={formatTickY}
			/>
			<MultiLine bind:hovered={hovered} bind:selected={selected}/>
		</Svg>

	</LayerCake>
</div>