<script>
	import { onMount, setContext } from 'svelte';
	import mapbox from 'mapbox-gl';

	export let location = {
		bounds: [[-10.653041, 49.815042], [1.643370, 60.928626]]  // GB & Ireland bounding box
	};
	export let style;
	export let minzoom = 0;
	export let maxzoom = 14;
	
	let map;
	let container;
	let options;
	
	setContext('map', {
		getMap: () => map
	});
	
	if (location.bounds) {
		options = { bounds: location.bounds };
	} else if (location.lon && location.lat) {
		options = {
			center: [location.lon, location.lat]
		}
		if (location.zoom) {
			options.zoom = location.zoom;
		}
	};

	onMount(() => {
		const link = document.createElement('link');
		link.rel = 'stylesheet';
		link.href = 'https://unpkg.com/mapbox-gl/dist/mapbox-gl.css';

		link.onload = () => {
			map = new mapbox.Map({
				container,
				style: style,
				minZoom: minzoom,
				maxZoom: maxzoom,
				...options
			});
		};

		document.head.appendChild(link);

		return () => {
			map.remove();
			link.parentNode.removeChild(link);
		};
	});
</script>

<style>
	div {
		width: 65%;
		height: 100%;
		position: fixed;
		right: 0;
	}
	@media (max-width: 900px) {
		div {
			width: 55%;
		}
	}
	@media (max-width: 600px) {
		div {
			width: 100%;
			height: 60%;
			position: absolute;
		}
	}
</style>

<div bind:this={container}>
	{#if map}
		<slot></slot>
	{/if}
</div>